*{
    margin: 0;
    padding: 0;
}
.container{
    width: 250px;
    
    border: 2px solid black;
    border-radius: 0px 10px 10px 10px ;
    margin: 10px;
    position: relative;
}
.container>div{
    width: 90%;
    margin: auto;
}
img{
    margin: 10px auto;
    width: 100%;
    text-align: center;
    background: transparent;
    border-radius: 12px;
}
.time{
    display: flex;
    justify-content: space-between;
    margin: 10px auto;
    align-items: center;

}
.offer{
    margin: 10px auto;
    text-align: center;
}
.promote{
    background-color: rgb(58, 60, 65);
    color: rgb(255, 255, 255);
    /* border-color: rgb(30, 32, 35) transparent; */
    align-items: center;
    width: 110px;
    padding: 3px;
    margin: 0;
    box-sizing: border-box;
    border: 2px solid white;

    position: absolute;
    top: 0px;
    left: 0px;
    visibility: hidden;

}
.star{
    background-color: #48c479;
    padding: 3px;
}